//   
import React from 'react';
import { ComponentPanel
	   , FlexPanel
	   , FullPanel
	   , SubFullPanel
	   , RelativeGroup
	   , TabPanel
	   , Tabs
	   , Grid 
	   , Diagram            } from 'components';
import {BasicButton as Button} from 'components';
import { ComLib
	   , DataLib
	   , StrLib
	   , TransManager
	   , newScrmObj            } from 'common';
import BOT010100 from '../BOT010100';

class View extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			readyToLoadDiagram: false,
			dsSnroInitTTSList: DataLib.datalist.getInstance(),
			dsSnroNodeList   : DataLib.datalist.getInstance(),
			dsSnroLinkList   : DataLib.datalist.getInstance(),
			dsSnroList       : [],
			actionData : [],
			btnProps : {
				btnSearch : {
					id       : 'btnSearch',
					disabled : false,
					value    : '',
					hidden   : false
				},
				btnBigSave : {
					id       : 'btnBigSave',
					disabled : false,
					value    : '',
					hidden   : false
				},
	
				btnSmlSave : {
					id       : 'btnSmlSave',
					disabled : false,
					value    : '',
					hidden   : false
				},
			},	
			
			grdProps : {		
				grdSnroInitProcess : {
					id : 'grdSnroInitProcess',
					areaName : ' ',
					header: [						
								{headerName: '',field: 'SNRO_CD',	       colId: 'SNRO_CD',		editable: false, width: 80 },
								{headerName: '',field: 'PROCESS_TP_NM',    colId: 'PROCESS_TP_NM',	editable: false, width: 100},
								{headerName: ' ',	field: 'SNRO_EXPL',	colId: 'SNRO_EXPL',	    editable: false, width: 300 },
								{headerName: '', field: 'PROCESS'   ,	colId: 'PROCESS'   ,	editable: true, width: 90,
									cellRenderer: 'actionButton', 
									fiiled: true,
									color: 'blue'},
							],
					height: '670px'
				},
			},
			selectboxProps : {
				selUseYn : {
					id       : 'selUseYn',
					value    : '',
					dataset  : ComLib.convComboList(ComLib.getCommCodeList("USE_FLAG"), newScrmObj.constants.select.argument.all),
					width    : 80,
					selected : 0,
					disabled : false
				}
			},			
			textFieldProps : {
				iptBigCdNm : {
					id          : 'iptBigCdNm',
					name        : 'iptBigCdNm',
					value       : '',
					placeholder : '/',
					minLength   : 1,
					maxLength   : 12,
					readOnly    : false,
					disabled    : false
				}
			},
		}
	}
	/*------------------------------------------------------------------------------------------------*
		0) Custom Event Zone 

	 ------------------------------------------------------------------------------------------------*/

	/*------------------------------------------------------------------------------------------------*
		1) componentDidMount () => init    
		=>   ,  ->    this.setState  ,    render()   ->   
	 ------------------------------------------------------------------------------------------------*/
	componentDidMount () {
		this.transaction("BOT010000_R00")
	}
	/*------------------------------------------------------------------------------------------------*
		2) componentDidUpdate () =>     (    )
		=> prevProps  props    ->    setState  ,    ->  setState   
	 ------------------------------------------------------------------------------------------------*/
	componentDidUpdate (prevProps, prevState, snapshot) {
		// console.log("updated!!");
		// console.log(this.state.dsGrp);
	}
	/*------------------------------------------------------------------------------------------------*
		3) componentWillUnmount () =>      
		=>  ,      ->    setState  
	 ------------------------------------------------------------------------------------------------*/
	 componentWillUnmount () {

	}

	/*------------------------------------------------------------------------------------------------*/
	// [3. validation Event Zone]
	//  - validation  
	/*------------------------------------------------------------------------------------------------*/
	validation = (...params) => {
		let transId = params[0];
		switch (transId) {
		case 'BOT010000_R01':

			break;
		default: break;
		}

		return true;
	}
	/*------------------------------------------------------------------------------------------------*/
	// [4. transaction Event Zone]
	//  - transaction  
	/*------------------------------------------------------------------------------------------------*/
	transaction = (...params) => {
		let transId = params[0];

		let transManager = new TransManager();

		transManager.setTransId (transId);
		transManager.setTransUrl(transManager.constants.url.common);
		transManager.setCallBack(this.callback);

		try  {
			switch (transId) {
			case 'BOT010000_R00':
		
				transManager.addConfig  ({
					dao        : transManager.constants.dao.base,
					crudh      : transManager.constants.crudh.read,
					sqlmapid   : "BOT.R_getSnroTTS",
					datasetsend: "dsEmpty",
					datasetrecv: "dsSnroInitTTSListRecv"
				});

				transManager.addDataset('dsEmpty', [{}]);
				transManager.agent();

				break;			
			case 'BOT010000_R01':

				// transManager.setTransId("test");
				// transManager.setTransUrl(transManager.constants.url.chilkat);
				// transManager.setCallBack(this.callback);
				// transManager.addConfig({
				// 	dao: transManager.constants.dao.base,
				// 	crudh: transManager.constants.crudh.sttSearch,
				// 	datasetsend: "test",
				// });
					
				// transManager.addDataset('test', [{test:"test"}]);
				// transManager.agent();

				transManager.addConfig  ({
					dao        : transManager.constants.dao.base,
					crudh      : transManager.constants.crudh.read,
					sqlmapid   : "BOT.R_getSnroNode",
					datasetsend: "dsEmpty",
					datasetrecv: "dsSnroNodeListRecv"
				});

				transManager.addConfig  ({
					dao        : transManager.constants.dao.base,
					crudh      : transManager.constants.crudh.read,
					sqlmapid   : "BOT.R_getSnroLink",
					datasetsend: "dsEmpty",
					datasetrecv: "dsSnroLinkListRecv"
				});

				transManager.addDataset('dsEmpty', [{}]);
				transManager.agent();

				break
			default: break;
			}
		} catch (err) {

		}
	}
	/*------------------------------------------------------------------------------------------------*/
	// [5. Callback Event Zone]
	//  - Callback  
	// BOT010000_R01 :   
	// BOT010000_R02 :   
	// BOT010000_H01 :   
	// BOT010000_H02 :   
	/*------------------------------------------------------------------------------------------------*/
	callback = (res) => {	

		switch (res.id) {
		case 'BOT010000_R00':
			if (res.data.dsSnroInitTTSListRecv.length > 0) {
				let dsSnroInitTTSListRecv = res.data.dsSnroInitTTSListRecv;

				ComLib.setStateInitRecords(this, "dsSnroInitTTSList", dsSnroInitTTSListRecv);
				
			} else {
				ComLib.setStateInitRecords(this, "dsSnroInitTTSList", []);

			}
			break;
			
		case 'BOT010000_R01':	
			if (res.data.dsSnroNodeListRecv.length > 0) {
				let { dsSnroNodeListRecv, dsSnroLinkListRecv } = res.data;
				ComLib.setStateInitRecords(this, "dsSnroNodeList", dsSnroNodeListRecv);
				ComLib.setStateInitRecords(this, "dsSnroLinkList", dsSnroLinkListRecv);
				let dsSnroList = {node: null, link: null};
				dsSnroList.node = this.state.dsSnroNodeList.records;
				dsSnroList.link = this.state.dsSnroLinkList.records;
				

				// react diagram npm   2   
				//        
				//    props    componet  2       
				this.setState({...this.state, dsSnroList: dsSnroList})
				this.setState({...this.state, dsSnroList: dsSnroList})

			} else {
				ComLib.setStateInitRecords(this, "dsSnroNodeList", []);
				ComLib.setStateInitRecords(this, "dsSnroLinkList", []);

			}
			break;

		default : break;
		}
	}
	/*------------------------------------------------------------------------------------------------*/
	// [6. event Zone]
	//  -  Component event 
	/*------------------------------------------------------------------------------------------------*/
	event = {
		//  
		button : {
			onClick : (e) => {
				switch (e.target.id) {
				case "btnDiagramSrch":
					if (this.validation("BOT010000_R01")) this.transaction("BOT010000_R01");
				
					break;

				case "btnBigSave":
					if (this.validation("BOT010000_H01")) this.transaction("BOT010000_H01");

					break;

				case "btnSmlSave":
					if (this.validation("BOT010000_H02")) this.transaction("BOT010000_H02");
					
					break;	
						
				default : break;
				}
			}
		},
		diagram : {
			onSave : (e) => {
				let transNode = [];
				let transLink = [];
				let {node, link} = e;

				for (let i = 0; i < node.length; i ++) {
					transNode.push(node[i]);
					transNode[i].ports = JSON.stringify(node[i].ports);
				}

				for (let i = 0; i < link.length; i ++) {
					transLink.push(link[i]);
					transLink[i].points = JSON.stringify(link[i].points);
				}

			}
		}
	}
	/*------------------------------------------------------------------------------------------------*/
	// [7. render Zone]
	//  -    
	/*------------------------------------------------------------------------------------------------*/
	render () {
		return (
			<React.Fragment>								
				<FullPanel>		
					<SubFullPanel>	
						<FlexPanel>	
							<ComponentPanel>
								<Button
									color='purple' 
									size ='md'
									fiiled= {true}
									id = {"btnDiagramSrch"}
									value = {""}
									disabled = {false}
									hidden = {false}
									onClick = {this.event.button.onClick}
									ml = {5}
								/>
								<Diagram
									height     = "700px"
									tts        = {this.state.dsSnroInitTTSList.records}
									onSave     = {this.event.diagram.onSave}
									dsSnroList = {this.state.dsSnroList}
									onLoad     = {this.state.readyToLoadDiagram}										
								/>	
							</ComponentPanel>
						</FlexPanel>
					</SubFullPanel>	
				</FullPanel>
			</React.Fragment>
		)
	}
}
export default View;