// 
import React from 'react';
import {
	ComponentPanel, RFloatArea, RelativeGroup
} from 'components';
// 
import {Label} from 'components';
import {Grid} from 'components';
import {DataLib} from 'common';

class View extends React.Component {
	constructor(props) {
		super();

		this.state = {
			dsStudyLogInfo : DataLib.datalist.getInstance()
		}
	}
	componentDidMount () {

	}
	render () {
		return (
			<React.Fragment>
				<ComponentPanel>
					<RelativeGroup>
						<RFloatArea>
							<Label value=" 3   ."/>
						</RFloatArea>
					</RelativeGroup>
					<Grid
						height= "300px"
						data = {this.state.dsStudyLogInfo}
						header = {
							[
								{headerName: '',	field: 'BIZ_PSB_YN',	colId: 'BIZ_PSB_YN' },
								{headerName: '',		field: 'TEAM_CD',		colId: 'TEAM_CD',	},
								{headerName: '',		field: 'USR_CD',		colId: 'USR_CD',	},
							]
						}
					/>
				</ComponentPanel>
			</React.Fragment>
		)
	}
}

export default View;