//  
import React from 'react';
import { FullPanel
	   , SubFullPanel
	   , RelativeGroup
	   , TabPanel
	   , Tabs } from 'components';	   
import { ComLib
	   , DataLib            } from 'common';
import SUP080100 from '../SUP080100';
import SUP080200 from '../SUP080200';
import SUP080300 from '../SUP080300';

class View extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			dsSysLogList   : DataLib.datalist.getInstance(),
			dsUserLogList  : DataLib.datalist.getInstance(),
			dsTrainLogList : DataLib.datalist.getInstance(),	
		}

	}
	/*------------------------------------------------------------------------------------------------*
		1) componentDidMount () => init    
		=>   ,  ->    this.setState  ,    render()   ->   
	 ------------------------------------------------------------------------------------------------*/
	componentDidMount () {
		
	}
	/*------------------------------------------------------------------------------------------------*
		2) componentDidUpdate () =>     (    )
		=> prevProps  props    ->    setState  ,    ->  setState   
	 ------------------------------------------------------------------------------------------------*/
	componentDidUpdate (prevProps, prevState, snapshot) {

	}
	/*------------------------------------------------------------------------------------------------*
		3) componentWillUnmount () =>      
		=>  ,      ->    setState  
	 ------------------------------------------------------------------------------------------------*/
	 componentWillUnmount () {

	}

	/*------------------------------------------------------------------------------------------------*/
	// [3. validation Event Zone]
	//  - validation  
	/*------------------------------------------------------------------------------------------------*/

	/*------------------------------------------------------------------------------------------------*/
	// [4. transaction Event Zone]
	//  - transaction  
	/*------------------------------------------------------------------------------------------------*/

	/*------------------------------------------------------------------------------------------------*/
	// [5. Callback Event Zone]
	//  - Callback  
	/*------------------------------------------------------------------------------------------------*/
	
	/*------------------------------------------------------------------------------------------------*/
	// [6. event Zone]
	//  -  Component event 
	/*------------------------------------------------------------------------------------------------*/
	event = {
		tab : {
			onClick : (e) => {
			},
			onLoadData : (e) => {
				console.log(e);

				switch (e.id) {
				case 'SYS':
					ComLib.setStateInitRecords(this, "dsSysLogList", e.data);
					break;
				case 'USR':
					ComLib.setStateInitRecords(this, "dsUserLogList", e.data);
					break;
				case 'TRAIN':
					ComLib.setStateInitRecords(this, "dsTrainLogList", e.data);
					break;
				default : break;
				}
				

			}
		},
	}
	/*------------------------------------------------------------------------------------------------*/
	// [7. render Zone]
	//  -    
	/*------------------------------------------------------------------------------------------------*/
	render () {
		return (
			<React.Fragment>
				<FullPanel>				
					<SubFullPanel>	
							<RelativeGroup>					
								<Tabs tabWidth='100px' onClick = {this.event.tab.onClick}>
									<TabPanel id = {'SUP080100'} index={0} label={' '}>
										<SUP080100										
											sysLogList = {this.state.dsSysLogList}
											onLoadData = {this.event.tab.onLoadData}
										/>
									</TabPanel>
									<TabPanel id = {'SUP080200'} index={1} label={' '}>
										<SUP080200										
											userLogList = {this.state.dsUserLogList}
											onLoadData  = {this.event.tab.onLoadData}
										/>
									</TabPanel>
									<TabPanel id = {'SUP080300'} index={2} label={' '}>
										<SUP080300										
											trainLogList = {this.state.dsTrainLogList}
											onLoadData   = {this.event.tab.onLoadData}
										/>
									</TabPanel>									
								</Tabs>	
							</RelativeGroup>	
					</SubFullPanel>
				</FullPanel>
			</React.Fragment>
		)
	}
}
export default View;